import { useParams } from "react-router";

import { useNavigate } from "react-router-dom";

export function PlanillaPrestamo(){
    const navigate = useNavigate()
    const params = useParams()
console.log(params)
let stylePrest = {
    width: '500px',
    height: '500px',
    borderRadius: "solid 1px",
    
}
let styleDiv = {
    display: 'flex',
justifyContent: 'space-around'
}
    return(
        <div>
            <button onClick={() => navigate(`/home/prestamos`) }>volver</button>
        <div style={stylePrest}>
             <h1>Museo de Cs Naturales</h1>
             <h5>El señor/a {params.emisor}</h5> 
             <h5>genera prestamo de tipo {params.tipo}</h5> 
             <h5>el dia {params.fecha}</h5> 
             <h5>de especimen Nro: {params.nro}</h5>
             <h5> a {params.receptor} del instituto {params.insti}</h5>
             { params?.comen.length>1 ? <>
             <h5>con estos comentarios :</h5>
             <h5> {params.comen} </h5></>: null}
             <h5> con fecha de devolucion {params.fechade}</h5>
             <br></br>
             <br></br>
             <br></br>
             <br></br>
             <br></br>
            <div style={styleDiv}>
                <div><p>_________________</p>
                <p>EMISOR</p>
                </div>
                <div><p>_________________</p>
                <p>RECEPTOR</p>
                </div>
            </div>
             

            </div>
        </div>
    )
}