import react from 'react';
import './menu.css';
import {FiSettings} from "react-icons/fi";

import { useAuth0 } from "@auth0/auth0-react";
import tablinum from "../componentes/TABLINUM.jpg"
import { LogOutButton } from './landingPage/logout';
import { useSelector } from 'react-redux';
import { useNavigate } from "react-router-dom";
import { BiQrScan } from "react-icons/bi";

import React, { useEffect, useState} from "react";
import LandingPageTablinum from './landingPage/landingPage';

export default function Menu() {
    const { isAuthenticated, user,isLoading} = useAuth0();
    const userD = useSelector((state)=> state.usuario)
    const navigate = useNavigate()
    const usuario1 = useSelector((state)=> state.usuario)
    const codigo = useSelector((state)=> state.qrs)
    /* if (isLoading) {
        return <div> Loading...</div>;
      } */

 
    return (
        <div class='padre'>
            { isAuthenticated ? 
                <div>
            <div class='nav-Bar1'>
                    
                <div class='logo-home'>
                    <img src={tablinum} width="100%" height="100%" onClick={() => navigate(`/home`) }/>
                </div>
                    {
                        userD.nivel>3 ?
                <div class='boton-catalogo'>
                <button type="button" class="botonCito"data-toggle="button" aria-pressed="false" autoComplete="off"onClick={() => navigate(`/home`) }>Catalogo Especimenes</button>
                </div>:
                <div class='botones'>
                
                <button type="button" class="botonCito"data-toggle="button" aria-pressed="false" autoComplete="off"onClick={() => navigate(`/home`) }>Catalogo Especimenes</button>
               
                <button type="button" class="botonCito"data-toggle="button" aria-pressed="false" autoComplete="off"onClick={() => navigate(`/home/bochon`) }>Catalogo Bochones</button>
              
                <button type="button" class="botonCito"data-toggle="button" aria-pressed="false" autoComplete="off"onClick={() => navigate(`/home/crear`) }>Crear</button>
               
                <button type="button" class="botonCito"data-toggle="button" aria-pressed="false" autoComplete="off"onClick={() => navigate(`/home/prestamos`) }>Prestamos</button>
               
                {
                 codigo[0] ?  <BiQrScan color='white' width='80px' onClick={() => navigate(`/home/plantillaqr`) }/> : null
                }
                </div>

                    }
                
                
                <div class='perfil'>
                    {user?.picture ? <img src={user?.picture} class='img-perfil' width='50%' height='50%'></img> : <></>}
                       {/*  <img src={user?.picture} class='img-perfil' width='50%' height='50%'></img> */}
                          
                           <FiSettings color='white' onClick={() => navigate(`/home/setting/${usuario1.id}`) }/>
                           

                            <LogOutButton/>
                            </div>
                    </div>   
                </div>: navigate('/')
  }
           </div>
    )
}