

import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import './plantillaqr.css'
export function Plantillaqr(){
    const codigos = useSelector((state)=> state.qrs)
   
    const navigate = useNavigate()
    function func() {
        return ( ( ( 1+Math.random() ) * 0x10000 ) | 0 ).toString( 16 ).substring( 1 );
        }
       
    return(

        <div>
            <div>
                <button onClick={() => navigate(`/home`) }>volver</button>
              

               
            </div>
            <div class='div-qr' id='div-imprimir'>
                { 
                codigos?.map(el => {
                    let UUID = (func() + func() + "-" + func() + "-3" + func().substr(0,2) + "-" + func() + "-" + func() + func() + func()).toLowerCase();
                    return <div class='qr'>
                            <h5>especimen: {el}</h5>
                            <img src={`http://api.qrserver.com/v1/create-qr-code/?data=https://tablinum-sj.herokuapp.com/home/qr/${UUID}/${el}&size=100x100`}></img>
                    </div>
                })
                }
            </div>
        </div>
    )
}